<template>
  <div id="home-component" class="container-fluid"  style="background: #e3e5eb;">
      <div class="row">
            <div class="col-lg-12 columna-12 pt-0" style="background: #e3e5eb;">
              <Carousel  :images="slideHome"/>
            </div>
            <div class="col-lg-12 columna-12" style="background: #e3e5eb;">
                <TitleCard 
                title="Cursos Matriculados"
                :dark="true"
                color="#563d7c"
                 />

                <CoursesList  :courses="coursesHome"/> 
            </div> 
      </div>
  </div>
</template>

<script>
import Carousel from '../../../components/IntranetComponents/Carousel/Carousel';
import TitleCard from '../../../components/IntranetComponents/Cards/TitleCard';
import CoursesList from '../../../components/IntranetComponents/Cards/CoursesList';

import { mapState, mapGetters } from 'vuex';

export default {
    components: {
        Carousel,
        TitleCard,
        CoursesList
    },
    mounted(){
        this.$store.dispatch('getCoursesHome');
    },
    computed: {
        ...mapState(['slideHome', 'coursesHome']),
        ...mapGetters({
            images: 'slideHome',
            cursosList: 'coursesHome'
        }),
        slideHome: {
            get() { return this.images },
            set(value) { return value }
        },
        coursesHome: {
            get() { return this.cursosList },
            set(value) { return value }
        }

    },
}
</script>

